<template>
  <!--지점 상세 안내-->
  <div class="chatStore">
    <div class="csTits">
      <a @click="onClickMore" class="locBtns" >{{ data.storNm }}</a>
      <a
        @click="onClickPhone"
        :href="`tel:${data.storTelNo}`"
        role="button"
        class="telBtns"
      >
        {{ $t('common.문의하기') }}
      </a>
      <a
        @click="onClickMap"
        data-id="mapViewPopup"
        class="mapBtns layerPopupButton"
        aria-haspopup="true"
      >
        {{ $t('common.지도보기') }}
      </a>
    </div>
    <div class="csTxts">
      <b>{{ $t('common.주소') }}</b>
      <p>{{ data.storAddr }} / TEL : {{ data.storTelNo }}</p>
      <b>{{ $t('common.운영시간') }}</b>
      <p v-html="data.storBusinessHour"></p>
    </div>
    <ul class="csBtns">
      <li>
        <a
          @click="onClickBrand"
          :class="isOnConsulting ? 'cb-disabled-button' : ''"
          role="button"
        >
          {{ $t('branch_info.brand_search') }}
        </a>
      </li>
      <li>
        <a
          @click="onClickBenefit"
          :class="isOnConsulting ? 'cb-disabled-button' : ''"
          role="button"
        >
          {{ $t('branch_info.benefit_search') }}
        </a>
      </li>
    </ul>
  </div>
  <!--//지점 상세 안내-->
</template>

<script>

import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'BranchDetailCard',
  mixins: [consultingStatus],
  props: {
    data: Object,
  },
  mounted() {
    this.$uiHandler.popup.popupOn();
  },
  methods: {
    onClickMore() {
      if (this.isOnConsulting) {
        return;
      }

      if (this.data.tags.more) {
        this.$analyticsHandler.apply(this.data.tags.more);
      }
      window.cbMoveParent(this.data.url);
    },
    onClickPhone() {
      if (this.isOnConsulting) {
        return;
      }

      if (this.data.tags.phone) {
        this.$analyticsHandler.apply(this.data.tags.phone);
      }
    },
    onClickMap() {
      if (this.isOnConsulting) {
        return;
      }
      if (this.data.tags.mapUrl) {
        this.$analyticsHandler.apply(this.data.tags.mapUrl);
      }

      this.$store.commit('SHOW_NMAP', this.data); // test code
    },
    onClickBrand() {
      if (this.isOnConsulting) {
        return;
      }

      if (this.data.tags.brandButton) {
        this.$analyticsHandler.apply(this.data.tags.brandButton);
      }

      this.$store.dispatch('SEND_MESSAGE', `${this.data.storNm} ${this.$t('branch_info.brand_search')}`);
    },
    onClickBenefit() {
      if (this.isOnConsulting) {
        return;
      }

      if (this.data.tags.benefitButton) {
        this.$analyticsHandler.apply(this.data.tags.benefitButton);
      }

      this.$store.dispatch('SEND_MESSAGE', `${this.data.storNm} ${this.$t('branch_info.benefit_search')}`);
    },
  },
};
</script>